import cookie from "js-cookie";
import _ from "lodash";
import React, { useMemo, useState, useEffect, useCallback } from "react";
import PulseLoader from "react-spinners/PulseLoader";
import { ReactComponent as MailIcon } from "#assets/shared/mail.svg";
import { ReactComponent as ArrowIcon } from "#assets/shared/left_arrow.svg";
import { emailValidator, openLinkInNewTab } from "#utils/shared";
import { Checkbox } from "#shared/ui";
import { useRootStore, useCreateRoutingCallback } from "#shared/hooks";
import { smartCodeVWO } from "#utils/smartCodeVWO";
import {
    AuthContainer,
    AuthHeaderTitleLabel,
    Input,
    AuthAddonLabel,
    AuthAddonLabelInternational,
    InternationalFlags,
    SubmitButton,
    ErrorLabelContainer,
    AuthErrorLabel,
    LeftArrow,
    NameInputContainer,
    CheckboxControl,
} from "./styled";
import GoogleSigninWidget from "./GoogleButton";
import { ContainerButton, ButtonLabel } from "./GoogleButton/GoogleSigninWidget.styled";
import { AUTH_TYPE, EmailCaptureCookie } from "../../../utils/constants";
import internationalSvg from "../assets/international.svg";
import { useTranslation } from "react-i18next";
import posthog from "posthog-js";

const OktaAuthRegister = ({ loading, setLoading, oktaAuth, onSuccess }) => {
    const { t } = useTranslation("login");

    const store = useRootStore();
    const routeTo = useCreateRoutingCallback();

    const [flipMode, setFlipMode] = useState(0); // 0: start, 1: email, 2: password, 3: username
    const [fName, setfName] = useState("");
    const [lName, setLName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [nameInvalid, setNameInvalid] = useState({ fInput: false, msg: "" });
    const [emailInvalid, setEmailInvalid] = useState({ input: false, msg: "" });
    const [passwordInvalid, setPasswordInvalid] = useState({
        input: false,
        msg: [null, null, null, null],
    });
    const [registerInvalid, setRegisterInvalid] = useState({ msg: "" });
    const [isSubscribeChecked, setSubscribeCheck] = useState(true);
    const [showPassword, setShowPassword] = useState(true);

    const headerTitle = useMemo(
        () =>
            _.cond([
                [(n) => n === 1, _.constant(t("okta-register.header-email"))],
                [(n) => n === 2, _.constant(t("okta-register.header-password"))],
                [(n) => n === 3, _.constant(t("okta-register.header-name"))],
                [_.stubTrue, _.constant(t("okta-register.header-invest"))],
            ])(flipMode),
        [flipMode],
    );

    /**
     * If being directed from email capture Landing Page
     * (1) Set the email value
     * (2) Advance flow to password input
     * (3) Remove cookie
     */
    useEffect(() => {
        if (flipMode === 0) {
            smartCodeVWO();
        }
        const emailCaptureCookie = cookie.get(EmailCaptureCookie.KEY);
        if (!_.isNil(emailCaptureCookie)) {
            setEmail(emailCaptureCookie);
            setFlipMode(2);

            store.tracking.gtm.trackPixelGetStarted();

            cookie.remove(EmailCaptureCookie.KEY, EmailCaptureCookie.CONFIG);
        }
    }, []);

    const enterKeyPress = (event) => {
        if (event.keyCode === 13) {
            handleRegister();
        }
    };

    const passwordValidation = (passw = "") => {
        const hasEightLength = !!passw.match(/^.{8,15}.*$/);
        const hasOneDigit = !!passw.match(/^(?=.*[0-9]).*$/);
        const hasUpperCase = !!passw.match(/^(?=.*[A-Z]).*$/);
        const hasSpecialCharacter = !!passw.match(/^(?=.*[@#$%^&+\-=!"'()*+,./:;<>?[\]^_`{}|~]).*$/);

        const isPasswordValid = hasEightLength && hasOneDigit && hasUpperCase && hasSpecialCharacter;

        return [isPasswordValid, hasEightLength, hasOneDigit, hasUpperCase, hasSpecialCharacter];
    };

    const onEnterPassword = useCallback((value) => {
        const validationResult = passwordValidation(value);
        if (!value.length) {
            setPasswordInvalid({ input: false, msg: [null, null, null, null] });
        }

        if (value.length >= 3) {
            setPasswordInvalid({
                input: false,
                msg: [validationResult[1], validationResult[2], validationResult[3], validationResult[4]],
            });
        }

        setPassword(value);
    }, []);

    const handleRegister = async () => {
        if (flipMode < 3) {
            if (flipMode === 1) {
                if (email === "") {
                    setEmailInvalid({ input: true, msg: t("okta-register.empty-email") });
                    return;
                }

                if (!emailValidator(email)) {
                    setEmailInvalid({
                        input: true,
                        msg: t("okta-register.invalid-email"),
                    });
                    return;
                }
                store.tracking.gtm.trackPixelGetStarted(email);
                posthog.capture("email_entry", {});
            } else if (flipMode === 2) {
                const validationResult = passwordValidation(password);

                if (!validationResult[0]) {
                    setPasswordInvalid({
                        input: true,
                        msg: [validationResult[1], validationResult[2], validationResult[3], validationResult[4]],
                    });
                    return;
                }
                posthog.capture("password_entry", {});
            }

            setFlipMode(flipMode + 1);
            return;
        }

        if (_.isEmpty(fName) || _.isEmpty(lName)) {
            setNameInvalid({ fInput: true, msg: t("okta-register.error-name") });
            return;
        } else {
            posthog.capture("name_entry", {});
        }

        localStorage.setItem("signFrom", "normal");

        setLoading(true);

        try {
            const response = await store.auth.registerNewUserAccount({
                firstName: fName,
                lastName: lName,
                email,
                password,
            });

            const didRegisterFail = _.get(response, "code") >= 400;
            if (didRegisterFail) {
                setLoading(false);
                setPassword("");
                setRegisterInvalid({ msg: _.get(response, "message", "Something went wrong. Please try again") });
                setEmailInvalid({ ...emailInvalid, input: true });
                setPasswordInvalid({ input: false, msg: [null, null, null, null] }); // resets to initial state
                setFlipMode(1); // return to email input
                return;
            }

            oktaAuth
                .signInWithCredentials({
                    username: email,
                    password,
                })
                .then((res) => {
                    localStorage.setItem(AUTH_TYPE.KEY, AUTH_TYPE.SIGNUP);
                    setLoading(false);
                    onSuccess(res);
                })
                .catch((err) => {
                    setLoading(false);
                    routeTo("/login");
                });
        } catch (err) {
            setLoading(false);
        }
    };

    function handlePrivacyClick() {
        openLinkInNewTab("https://vinovest.co/privacy-policy");
    }

    function handleTermsClick() {
        openLinkInNewTab("https://vinovest.co/terms-conditions");
    }
    return (
        <AuthContainer>
            {flipMode !== 0 && (
                <LeftArrow onClick={() => setFlipMode(flipMode - 1)}>
                    <ArrowIcon />
                </LeftArrow>
            )}

            <AuthHeaderTitleLabel as="h1">{headerTitle}</AuthHeaderTitleLabel>
            {flipMode === 0 && (
                <>
                    <GoogleSigninWidget
                        type={AUTH_TYPE.SIGNUP}
                        trackPixelGetStarted={store.tracking.gtm.trackPixelGetStarted}
                    />

                    <ContainerButton normal onClick={() => setFlipMode(1)}>
                        <MailIcon style={{ position: "absolute", marginLeft: "20px" }} />
                        <ButtonLabel normal="true"> {t("okta-register.button-email")}</ButtonLabel>
                    </ContainerButton>

                    <div style={{ marginTop: "30px" }}>
                        <AuthAddonLabel isNotDecrator="true"> {t("okta-register.button-account")}</AuthAddonLabel>
                        <AuthAddonLabel
                            style={{ marginLeft: "4px", textDecoration: "underline" }}
                            onClick={() => routeTo("/login")}
                        >
                            {t("okta-register.button-login")}
                        </AuthAddonLabel>
                    </div>
                    <div>
                        <AuthAddonLabelInternational isNotDecrator>
                            {t("okta-register.international")}
                        </AuthAddonLabelInternational>
                        <InternationalFlags src={internationalSvg} alt="flags" />
                    </div>
                    <div>
                        <AuthAddonLabel isNotDecrator="true">{t("okta-register.disclaimer")}</AuthAddonLabel>
                        <AuthAddonLabel style={{ marginLeft: "4px" }} hasUnderline onClick={handlePrivacyClick}>
                            {t("okta-register.privacy")}
                        </AuthAddonLabel>
                        <AuthAddonLabel isNotDecrator="true" style={{ marginLeft: "4px" }}>
                            {t("okta-register.disclaimer-cont")}
                        </AuthAddonLabel>
                        <AuthAddonLabel style={{ marginLeft: "4px" }} hasUnderline onClick={handleTermsClick}>
                            {t("okta-register.terms")}
                        </AuthAddonLabel>
                        <AuthAddonLabel isNotDecrator="true" style={{ marginLeft: "4px" }}>
                            {t("okta-register.newsletter")}
                        </AuthAddonLabel>
                    </div>
                </>
            )}

            {flipMode === 1 && (
                <>
                    <Input
                        placeholder={t("okta-register.email")}
                        type="text"
                        value={email}
                        onKeyDown={enterKeyPress}
                        onChange={(e) => {
                            setEmail(e.target.value);
                            setEmailInvalid({ input: false, msg: "" });
                        }}
                        isInvalid={emailInvalid.input}
                    />
                    <ErrorLabelContainer>
                        <AuthErrorLabel isValid={_.isNil(emailInvalid.msg)}>{emailInvalid.msg}</AuthErrorLabel>
                        <AuthErrorLabel isValid={_.isNil(registerInvalid.msg)}>{registerInvalid.msg}</AuthErrorLabel>
                    </ErrorLabelContainer>
                </>
            )}

            {flipMode === 2 && (
                <>
                    <Input
                        placeholder={t("okta-register.password")}
                        type={showPassword ? "text" : "password"}
                        value={password}
                        onKeyDown={enterKeyPress}
                        onChange={(e) => {
                            onEnterPassword(e.target.value);
                        }}
                        isInvalid={passwordInvalid.input}
                    />

                    <ErrorLabelContainer>
                        <AuthErrorLabel>{t("okta-register.password-min")} </AuthErrorLabel>
                        <AuthErrorLabel isValid={passwordInvalid.msg[0]}>
                            {t("okta-register.password-char")},{" "}
                        </AuthErrorLabel>
                        <AuthErrorLabel isValid={passwordInvalid.msg[1]}>
                            {t("okta-register.password-num")},{" "}
                        </AuthErrorLabel>
                        <AuthErrorLabel isValid={passwordInvalid.msg[2]}>
                            {t("okta-register.password-upper")}{" "}
                        </AuthErrorLabel>
                        <AuthErrorLabel>{t("okta-register.password-cont")} </AuthErrorLabel>
                        <AuthErrorLabel isValid={passwordInvalid.msg[3]}>
                            {t("okta-register.password-special")}
                        </AuthErrorLabel>
                    </ErrorLabelContainer>
                </>
            )}

            {flipMode === 3 && (
                <>
                    <NameInputContainer>
                        <Input
                            placeholder={t("okta-register.placeholder-first")}
                            type="text"
                            value={fName}
                            onChange={(e) => {
                                setfName(e.target.value);
                                setNameInvalid({ fInput: false, msg: "" });
                            }}
                            isInvalid={nameInvalid.fInput}
                        />
                        <Input
                            placeholder={t("okta-register.placeholder-last")}
                            type="text"
                            value={lName}
                            onKeyDown={enterKeyPress}
                            onChange={(e) => {
                                setLName(e.target.value);
                                setNameInvalid({ lInput: false, msg: "" });
                            }}
                            isInvalid={nameInvalid.fInput}
                        />
                    </NameInputContainer>
                    <ErrorLabelContainer>
                        <AuthErrorLabel isValid={_.isNil(nameInvalid.msg)}>{nameInvalid.msg}</AuthErrorLabel>
                    </ErrorLabelContainer>
                </>
            )}

            {flipMode === 2 && (
                <CheckboxControl mTop={21}>
                    <Checkbox square checked={showPassword} onChange={() => setShowPassword(!showPassword)} />
                    <label>{flipMode == 1 ? t("okta-register.subscribe") : t("okta-register.show-password")}</label>
                </CheckboxControl>
            )}

            {flipMode !== 0 && (
                <SubmitButton disabled={loading} onClick={handleRegister}>
                    {loading ? (
                        <PulseLoader size={8} margin="10px" color="#828282" loading={loading} />
                    ) : (
                        t("okta-register.submit")
                    )}
                </SubmitButton>
            )}

            {flipMode === 1 && (
                <CheckboxControl mTop={21}>
                    <Checkbox
                        square
                        checked={isSubscribeChecked}
                        onChange={() => setSubscribeCheck(!isSubscribeChecked)}
                    />
                    <label>{flipMode === 1 ? t("okta-register.subscribe") : t("okta-register.show-password")}</label>
                </CheckboxControl>
            )}
        </AuthContainer>
    );
};

export default OktaAuthRegister;
